import React, {useState} from "react";
import AddIcon from '@mui/icons-material/Add';
import Fab from '@mui/material/Fab';
import Zoom from '@mui/material/Zoom';

function CreateNote(props) {
  const [isClicked, setisClicked] = useState(false);
  const [inputText, setinputText] = useState({
    title: "",
    note: ""
  });

  function updateInput(event) {
    const {value, name} = event.target;
    setinputText(prevValue => {
      return {
        ...prevValue,
        [name]: value
      };
    });
  }
  function submitForm(event) {
    props.onAdd(inputText);
    event.preventDefault();
    setinputText({
      title: "",
      note: ""
    });
  }
  function expand() {
    setisClicked(true);
  }
  return (<div>
          <form className="create-note">
          {isClicked && (<input name="title" id="title" onChange={updateInput} value={inputText.title}
          placeholder="Title" />)}
          <textarea name="note" onChange={updateInput} value={inputText.note} onClick={expand}
          placeholder="Take a note..." rows={isClicked ? 3 : 1} />
          <Zoom in={isClicked}><Fab onClick={submitForm}><AddIcon /></Fab></Zoom>
          </form>
          </div>);
}

export default CreateNote;
